import { Nav, Navbar } from "react-bootstrap";

export default function Hambi() {
	return (
		<Navbar id="navBar" collapseOnSelect expand="true" bg="transparent" variant="dark">
			<Navbar.Toggle aria-controls="collapsed-navbar-nav" />
			<Navbar.Collapse id="responsive-navbar-nav">
				<Nav className="mr-auto" variant="light">
					<Nav.Link className="navLink" href="#lorem">Lorem</Nav.Link>
					<Nav.Link className="navLink" href="#ipsum">Ipsum</Nav.Link>
					<Nav.Link className="navLink" href="#dolor">Dolor</Nav.Link>
				</Nav>
			</Navbar.Collapse>
		</Navbar>
	);

}